  --tw-shadow: var(--tw-shadow-colored);
}
.transition {
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}
.transition-all {
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}
.transition-transform {
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}
.duration-300 {
  transition-duration: 300ms;
}
.ease-out {
  transition-timing-function: cubic-bezier(0, 0, 0.2, 1);
}

/* TestimonialSlider.css */
.custom-swiper .swiper-pagination-bullet {
  background-color: #7beec4;
  opacity: 0.1;
}

.custom-swiper .swiper-pagination-bullet-active {
  background-color: #10b981;
  opacity: 1;
  transform: scale(1.2);
}

/* Custom scrollbar styles for the entire website */
*::-webkit-scrollbar {
  width: 8px; /* Width of the scrollbar */
  height: 8px; /* Height of the scrollbar */
}

*::-webkit-scrollbar-track {
  background: rgba(20, 20, 20, 0.1); /* Background of the track */
  border-radius: 10px; /* Rounded corners for the track */
}

*::-webkit-scrollbar-thumb {
  background: #74e39d; /* Color of the scrollbar thumb */
  border-radius: 10px; /* Rounded corners for the thumb */
}

*::-webkit-scrollbar-thumb:hover {
  background: #3f9f55; /* Darker color on hover */
}

/* For Firefox */
* {
  scrollbar-width: thin; /* Makes the scrollbar thinner */
  scrollbar-color: #74e39d rgba(20, 20, 20, 0.1); /* Thumb color and track color */
}
